body
    {
        margin: 0;
    }

header
    {
        width: 100%;
        background: #4A94AB;
    }

#btn-menu
    {
        display: none;

    }

header label
    {
        display: none;
        width: 50px;
        height: 50px;
        padding: 10px;
        border-right: 1px solid #fff;
    }

header label:hover
    {
        cursor: pointer;
        background: rgba(0, 0, 0, 0.3);

    }

.menu ul
    {
        margin: 0;
        list-style: none;
        padding: 0;
        display: flex;
    }

.menu li
    {
        text-align: center;
        flex-grow: 1;
        display: block;
        padding: 15px 20px;
        color: #fff;
        text-decoration: none;
    }

.menu li:hover
    {
        cursor: pointer;
        background: rgba(0, 0, 0, 0.3);
    }
    
@media (max-width: 768px)
    {
        header label
            {
                display: block;
            }

        .menu
            {
                position: absolute;
                background: #4A94AB;
                width: 70%;
                margin-left: -70%;
                transition: all 0.5s;
            }
        
        .menu ul
            {
                flex-direction: column;
            }
        
        .menu li
            {
                border-top: 1px solid #fff;

            }

        #btn-menu:checked ~ .menu
            {
                margin: 0;
            }
    }